import $ from 'jquery'
export default {
  props: ["show"],
  methods: {
    closeFullpage(){
      this.$emit("closeFullpage")
    }
  },
  mounted(){
    window.addEventListener("keydown",function(evt){
      // console.log(evt.key)
      if (evt.key=="Escape"){
        this.closeFullpage()
      }
    })
  }
}



