Vue.mixin({
  methods: {
    strip_tags(txt) {
      let result = ("" + txt).replace(/(<([^>]+)>)/ig, "")
      return result
    },
    get_event_status_translate(txt) {

      switch (txt) {
        case "UNCONFIRMED":
          return { status: txt, label: "待確認", open: true, registed: true }
        case "CONFIRMED":
          return { status: txt, label: "已確認報名", open: true, registed: true }
        case 'registed':
          return { status: txt, label: "已報名", open: false, registed: true }
        case 'need login':
          return { status: txt, label: "登入後報名", open: false, registed: false }
        case 'not_open':
          return { status: txt, label: "非可報名期間", open: false, registed: false }
      }
      return { status: txt, label: "未報名", open: true, registed: false }
    },
    get_event_confirm_type_translate(txt) {
      switch (txt) {
        case "yes":
          return { status: txt, label: "錄取", symbol: "✓" }
        case "no":
          return { status: txt, label: "不錄取", symbol: "☓" }
        case 'pending':
          return { status: txt, label: "備取", symbol: "△" }
      }
      return { status: txt, label: "", symbol: "-" }
    },
    get_equipment_confirm_type_translate(txt) {
      switch (txt) {
        case "unconfirmed":
          return { status: txt, label: "未審核", symbol: "-" }
        case "yes":
          return { status: txt, label: "已核准", symbol: "✓" }
        case 'no':
          return { status: txt, label: "不核准", symbol: "☓" }
      }
      return { status: txt, label: "", symbol: "-" }
    },
    handleImageAdded(file, Editor, cursorLocation) {
      // An example of using FormData
      // NOTE: Your key could be different such as:
      // formData.append('file', file)
      // console.log("get picture!")
      var formData = new FormData();
      formData.append('file', file)
      console.log(file)

      axios({
        url: process.env.API_DOMAIN + '/api/upload',
        method: 'POST',
        data: formData
      })
        .then((result) => {
          let url = result.data // Get url from response
          Editor.insertEmbed(cursorLocation, 'image', url);
        })
        .catch((err) => {
          console.log(err);
        })
    },
    cssbg(url) {
      let use_url = (url || "").replace(/https?:\/\/dschool.ntu.edu.tw\/assets\//, "https://service.dschool.ntu.edu.tw/dschool_old_assets/")
      let result = {
        'background-image': `url("${use_url}")`,
        'background-position': 'center center',
        'background-size': 'cover'
      }
      // console.log(result)
      return result
    },

    bgcss(url) {
      return this.cssbg(url)
    },
    scrollTo(cname, custon_option) {
      let options = Object.assign({
        pan: 0
      },
        custon_option
      )
      $("html,body").animate({ scrollTop: $(cname).offset().top + options.pan })
    },

  }
})

$(window).scroll(() => {
  store.commit("setScrollTop", window.scrollY)
})


var timer = setTimeout(() => {
  store.commit("setLoading", false)
  clearTimeout(timer)
}, 2000);

window.onload = function () {
  if (!timer) {
    store.commit("setLoading", false)
  }
}

//取得網址的query string 轉換為物件
window.queryObject = (window.location.href.split("?")[1] || "")
  .split("&")
  .reduce((obj, text) => {
    obj[text.split("=")[0]] = text.split("=")[1];
    return obj;
  }, {});


console.log(queryObject.reset_token)
if (queryObject.reset_token) {
  store.commit("auth/setResetToken", queryObject.reset_token);
}
if (queryObject.reset_token) {
  store.commit("auth/setResetToken",queryObject.reset_token);
  store.commit("openMenu", "login");
}




//登入
store.dispatch("auth/init")





const app = new Vue({
  el: '#app',
  router,
  store,
  data: {
    registerMode: "student",
    schoolData
  },
  mounted() {
    store.dispatch('loadWebsite')
  },
  template: '<App/>'
});






