import {mapState,mapMutations, mapActions, mapGetters} from 'vuex'
import $ from 'jquery'
export default {
  props: {
    layout: {
      type: String,
      default: "card",
    },
    forceMode: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      registerData: {
      },
      registType: "student",
      loginData: {
        email:  localStorage.dschool_auth_user_email || "",
        password: "",
        
      },
      resetSendMailData:{
        email: "",
      },
      resetPasswordData:{
        email: localStorage.dschool_reset_user_email || "",
        password: "",
        password_confirmation: "",
        token: ""
      },
      mode: window.queryObject.reset_token?"reset_password":"login",
    }
  },
  computed: {
    ...mapState(['menuState','auth']),
    jobInforLabel(){
      if (this.registerData.jobcata=="學生"){
        return "學校系所"
      }
      return "職稱"
    },

    ...mapGetters({
      getUserPhoto: 'auth/getUserPhoto',
      isAdmin: 'auth/isAdmin',
      isEditor: 'auth/isEditor',
      canManage: 'auth/canManage',
      userGroup: 'auth/userGroup'
    }),

  },
  methods: {
    ...mapMutations({
      setMenuState: "setMenuState",
      setResetToken: "auth/setResetToken"
    }),
    ...mapActions({
      register: 'auth/register',
      login: 'auth/login',
      logout: 'auth/logout',
      loginFacebook: "auth/loginFacebook",
      authInit: "auth/init",
      resetSendMail: "auth/resetSendMail",
      resetPassword: "auth/resetPassword"
    }),
    panelLogin(){
      this.login(this.loginData).then(()=>{
        this.$emit("onLogin")

      })
    },

    userResetPassword(){
      let _this = this
      this.resetPassword({
        data: this.resetPasswordData,
        successHook: ()=>{
          this.$message.success( "密碼重設成功！" )

          setTimeout(()=>{
            _this.setResetToken("")
            _this.$set(this,"mode","login")
            //- window.location.href=window.location.href.split("?")[0]
            // this.$router.push("/")
            // this.setMenuState(false)
          },1500)
          
        },
        failHook: ()=>{
          this.$message.error( "密碼重設失敗..." )
        }
      })
    },
  },
  mounted(){
    // this.authInit()
    $("input.loginPwd").keypress((ev)=> {
          var keycode = (ev.keyCode ? ev.keyCode : ev.which);
          if (keycode == '13') {
            this.login(this.loginData)
          }
      })
  }
}



