import _ from 'lodash'
import {mapState} from 'vuex'

export default {
  data(){
    return {
      // posts: []  
      limit: 10
    }
  },
  methods:{
    // convert_cover(url){
    //   return (url || "").replace("http://dschool.ntu.edu.tw/assets/","/dschool_old_assets/")
    // }
  },
  computed:{
    ...mapState(['auth','user','posts','scrollTop']),
    is_admin(){
      return this.auth.user && this.auth.user.admingroup=='root' 
    },
    catas (){
      return this.posts.map(o=>o.cata).filter((d,i,arr)=>arr.indexOf(d)==i)
    },
    chunkedList(){
      let useList = this.posts.slice().reverse().slice(1,this.limit)
      let result = [
                    useList.filter((d,i)=>i%3==0),
                    useList.filter((d,i)=>i%3==1),
                    useList.filter((d,i)=>i%3==2)
                  ]
      return result
    },
    spotPost(){
      return this.posts
              .slice().reverse().filter(o=>o.show)[0]
    }
  },
  watch:{
    scrollTop(){
      if (this.scrollTop+$(window).height()*1.5>$(".endbar").offset().top){
        this.limit+=9
      }
    }
  },  
  mounted(){
    // axios.get("/api/post").then((res)=>{
    //   Vue.set(this,"posts",res.data)
      
    // })
    
  }
}



