import {mapState} from 'vuex'
import vue_lazy_table from '../Data/vue_lazy_table'
export default {
    data() {
      return {
        lists: [],
        event: {},
        keyword: "",
        tableRows: [
          "uuid -> __hide",
          "name -> 名字",
          "type -> 種類",
          "created_at -> 註冊時間",
          "updated_at -> __hide",
          "verified -> 驗證",
          "student_id -> 學號",
          "school -> 學校",
          "department -> 系級",
          "agency -> 單位",
          "phone -> 聯絡電話",
          "email -> 信箱",
          "status -> 狀態",
          "time -> 報名時間",
          "admingroup -> 權限",
        ]

      }
    },
    props: ['event_id'],
    mounted() {

        //取得報名清單
        axios.get(`/api/user/list`,{
          params:{
            token: this.auth.token
          }
        }).then((res)=>{
          Vue.set(this,"lists",res.data);
        })
    },
    computed:{
      ...mapState(['user','auth']),
      userList(){
        let r = JSON.parse(JSON.stringify(this.lists))
        r.forEach(user=>user.created_at = user.created_at.split(' ')[0])
        return r.filter(user=> (this.keyword=="" || JSON.stringify(user).indexOf(this.keyword)!=-1))
      }
    },
    components:{
      vue_lazy_table
    }
}



